/* -------------------------------------------------------------------------- 
	#. Floating Cart Button
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger {
  position: fixed;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  left: auto;
  right: 100px;
  bottom: 100px;
  z-index: var(--adfy_woofc_trigger_button_zindex);
  fill: #555555;
  color: #555555;
  cursor: pointer;
  background-color: white;
  border-radius: 0;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease;
}
#adfy__woofc-trigger .icon svg {
  height: 20px;
  width: 20px;
}
#adfy__woofc-trigger .badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: -15px;
  top: -10px;
  height: 23px;
  width: 23px;
  font-size: 13px;
  font-weight: normal;
  line-height: 1;
  color: white;
  border-radius: 3px;
  background-color: var(--adfy_woofc_primary_color);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.1);
}
#adfy__woofc-trigger:hover {
  fill: #898989;
  color: #898989;
  background-color: #DDD;
  box-shadow: 0px 0px 60px rgba(0, 0, 0, 0.1);
}
#adfy__woofc-trigger:focus {
  outline: none;
}

/* -------------------------------------------------------------------------- 
	#. Design
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger[data_style=round] {
  border-radius: 100%;
}
#adfy__woofc-trigger[data_style=round] .badge {
  top: -5px;
  left: -10px;
  border-radius: 100%;
}

#adfy__woofc-trigger[data_style=rounded] {
  border-radius: 5px;
}
#adfy__woofc-trigger[data_style=rounded] .badge {
  top: -5px;
  left: -10px;
}

/* -------------------------------------------------------------------------- 
	#. Label
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger[data_label=false] .label {
  display: none;
}

/* -------------------------------------------------------------------------- 
	#. Icon
  -------------------------------------------------------------------------- */
#adfy__woofc-trigger[data_icon=false] .icon {
  display: none;
}

/* -------------------------------------------------------------------------- 
	#. Floating Cart
  -------------------------------------------------------------------------- */
#adfy__floating-cart {
  display: block;
  position: fixed;
  background: white;
  background-color: white;
}
#adfy__floating-cart .adfy_woofc-inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  position: relative;
  padding: 30px;
}

#adfy__floating-cart[data_type=drawer] {
  top: 0;
  bottom: 0;
  height: 100%;
  min-height: 100vh;
  width: 400px;
  opacity: 1;
  visibility: visible;
}

/* -------------------------------------------------------------------------- 
	#. Position
  -------------------------------------------------------------------------- */
#adfy__floating-cart[data_type=modal] {
  z-index: var(--adfy_woo_floating_cart_zindex);
}

#adfy__floating-cart[data_type=drawer][data_position=left] {
  right: auto;
  left: -100vw;
}

#adfy__floating-cart[data_type=drawer][data_position=right] {
  left: auto;
  right: -100vw;
}

/* -------------------------------------------------------------------------- 
	#. Visiblity
  -------------------------------------------------------------------------- */
.adfy__woofc-visible #adfy__floating-cart {
  z-index: var(--adfy_woo_floating_cart_zindex);
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.3);
}

.adfy__woofc-visible #adfy__floating-cart[data_type=drawer][data_position=left] {
  right: auto;
  left: 0;
}

.adfy__woofc-visible #adfy__floating-cart[data_type=drawer][data_position=right] {
  left: auto;
  right: 0;
}

/* -------------------------------------------------------------------------- 
	#. Floating Cart Mask
  -------------------------------------------------------------------------- */
#adfy__woofc-overlay {
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease;
}

.adfy__woofc-visible #adfy__woofc-overlay {
  opacity: 1;
  visibility: visible;
  z-index: var(--adfy_woofc_overlaymask_zindex);
}

/* -------------------------------------------------------------------------- 
	#. Utility Class
  -------------------------------------------------------------------------- */
.adfy__hide-woofc {
  cursor: pointer;
}

/* -------------------------------------------------------------------------- 
	#. Slide to left
  -------------------------------------------------------------------------- */
@keyframes adfyWooFCSlideToLeft {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100vw);
  }
}